// Utility functions for time formatting

export interface DayHours {
  open: string;
  close: string;
  closed?: boolean;
  splitShift?: boolean; // 分段营业模式
  open2?: string; // 第二时段开门时间
  close2?: string; // 第二时段关门时间
}

export type OpeningHours = Record<string, DayHours>;

/**
 * Format opening hours for display
 * Returns the current day's hours or a summary
 */
export const formatOpeningHours = (openingHours: OpeningHours | string): string => {
  if (typeof openingHours === 'string') {
    return openingHours;
  }

  const days = ['sunday', 'monday', 'tuesday', 'wednesday', 'thursday', 'friday', 'saturday'];
  const currentDay = days[new Date().getDay()];
  
  const todayHours = openingHours[currentDay];
  
  if (!todayHours) {
    return 'Hours not available';
  }

  if (todayHours.closed) {
    return 'Closed today';
  }

  if (todayHours.splitShift) {
    return `${todayHours.open} - ${todayHours.close}, ${todayHours.open2} - ${todayHours.close2}`;
  }

  return `${todayHours.open} - ${todayHours.close}`;
};

/**
 * Get all opening hours formatted as a list
 */
export const formatAllOpeningHours = (openingHours: OpeningHours | string): string[] => {
  if (typeof openingHours === 'string') {
    return [openingHours];
  }

  const dayNames: Record<string, string> = {
    monday: 'Mon',
    tuesday: 'Tue',
    wednesday: 'Wed',
    thursday: 'Thu',
    friday: 'Fri',
    saturday: 'Sat',
    sunday: 'Sun'
  };

  const days = ['monday', 'tuesday', 'wednesday', 'thursday', 'friday', 'saturday', 'sunday'];
  
  return days.map(day => {
    const hours = openingHours[day];
    if (!hours) return `${dayNames[day]}: N/A`;
    if (hours.closed) return `${dayNames[day]}: Closed`;
    if (hours.splitShift) {
      return `${dayNames[day]}: ${hours.open} - ${hours.close}, ${hours.open2} - ${hours.close2}`;
    }
    return `${dayNames[day]}: ${hours.open} - ${hours.close}`;
  });
};
